<template>
  <div class="business-details">
    <el-tabs v-model="activeData.index" @tab-change="tabClick">
      <el-tab-pane
        v-for="item in tabList"
        :label="item.label"
        :name="item.value"
        :key="item.value"
      ></el-tab-pane>
    </el-tabs>
    <component :is="activeData.component"></component>
  </div>
</template>

<script lang="ts" setup>
import { ref, reactive, markRaw, defineAsyncComponent } from "vue";
import { LocalStorage } from "jz-tool-lib";

const CallRecordList = defineAsyncComponent(
  () =>
    import(
      "@/components/business-management/ospreyCloudCall/sub-customer/details/call-record/list.vue"
    )
);
const SpeechLibraryList = defineAsyncComponent(
  () =>
    import(
      "@/components/business-management/ospreyCloudCall/sub-customer/details/speech-library/list.vue"
    )
);
const CallingNumberList = defineAsyncComponent(
  () =>
    import(
      "@/components/business-management/ospreyCloudCall/sub-customer/details/calling-number/list.vue"
    )
);
const ThirdPartyDocking = defineAsyncComponent(
  () =>
    import(
      "@/components/business-management/ospreyCloudCall/sub-customer/details/third-party-docking/list.vue"
    )
);
defineOptions({
  name: "businessManagement.ospreyCloudCall.subCustomer.detail",
});
const agentInfo = LocalStorage.getItem("bd_agentInfo");
const activeData = reactive({
  index: 1,
  component: markRaw(CallRecordList),
});

const tabList = ref([
  {
    label: "通话记录",
    value: 1,
    componentName: markRaw(CallRecordList),
  },
  {
    label: "话术库",
    value: 2,
    componentName: markRaw(SpeechLibraryList),
  },
  {
    label: "主叫号码",
    value: 3,
    componentName: markRaw(CallingNumberList),
  },
]);
if (agentInfo.ecSwitch === 1) {
  tabList.value.push({
    label: "第三方对接",
    value: 4,
    componentName: markRaw(ThirdPartyDocking),
  });
}
const tabClick = (value: any) => {
  activeData.index = value;
  activeData.component = tabList.value[value - 1].componentName;
};
</script>
<style lang="scss" scoped></style>
